<!--  -->
<template>
    <div class="header">
        <div class="content-wrapper">
            <div class="avatar">
                <img width="64" height="64" :src="seller.avatar">
            </div>
            <div class="content">
                <div class="title">
                    <span class="brand"></span>
                    <span class="name">{{seller.name}}</span>
                </div>
                <div class="description">
                    {{seller.description}}/{{seller.deliveryTime}}分钟送达
                </div>
                <div class="support">
                    <span v-if="seller.supports" class="icon" :class="classMap[seller.supports[0].type]"></span>
                    <span v-if="seller.supports" class="text">{{seller.supports[0].description}}</span>
                </div>
            </div>
            <div class="supports-count">
                <span v-if="seller.supports" class="count" @click="showDetail()">{{seller.supports.length}}个</span>
                <i class="icon-keyboard_arrow_right"></i>
            </div>
        </div>
        <div class="bulletin-wrapper" @click="showDetail()">
            <span class="bulletin-icon"></span><span class="bulletin-text">{{seller.bulletin}}</span>
            <i class="icon-keyboard_arrow_right"></i>
        </div>
        <div class="background">
            <img :src="seller.avatar">
        </div>
        <transition name="fade">
            <div v-show="detailShow" class="detail">
                <div class="detail-wrapper clearfix">
                    <div class="detail-main">
                        <h1 class="name">{{seller.name}}</h1>
                        <div class="star-wrapper">
                            <star :size="48" :score="seller.score"></star>
                        </div>
                        <div class="title">
                            <div class="line"></div>
                            <div class="text">优惠信息</div>
                            <div class="line"></div>
                        </div>
                        <ul v-if="seller.supports" class="supports">
                            <li class="support-item" v-for="(item, index) in seller.supports" :key="index">
                                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                                <span class="text">{{seller.supports[index].description}}</span>
                            </li>
                        </ul>
                        <div class="title">
                            <div class="line"></div>
                            <div class="text">商家公告</div>
                            <div class="line"></div>
                        </div>
                        <div class="bulletin">
                            <div class="text">{{seller.bulletin}}</div>
                        </div>
                    </div>
                </div>
                <div class="detail-close" @click="hideDetail()">
                    <i class="icon-close"></i>
                </div>
            </div>
        </transition>
    </div>
</template>

<script type="text/ecmascript-6">
    import star from '../star/star';
    export default {
        props: {
            seller: {
                type: Object
            }
        },
        data() {
            return {
                detailShow: false
            };
        },
        methods: {
            showDetail() {
                this.detailShow = true;
            },
            hideDetail() {
                this.detailShow = false;
            }
        },
        components: {
            star
        },
        created () {
            this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
        }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin.styl";

    .header
        position: relative
        color: #ffffff
        overflow: hidden
        background-color: rgba(7, 17, 27, 0.3)
        .content-wrapper
            position: relative
            padding: 24px 12px 18px 24px
            font-size: 0
            .avatar
                display: inline-block
                vertical-align: top
                img
                    border-radius: 2px
            .content
                display: inline-block
                font-size: 14px
                margin-left: 16px
                .title
                    margin: 2px 0 8px 0
                    font-size: 0
                    .brand
                        display: inline-block
                        vertical-align: top
                        width: 30px
                        height: 18px
                        bg-image('brand')
                        background-size: 30px 18px
                        background-repeat: no-repeat
                    .name
                        margin-left: 6px
                        font-size: 16px
                        line-height: 18px
                        font-weight: bold
                .description
                    margin-bottom: 10px
                    font-size: 12px
                    line-height: 12px
                    font-weight: 200
                .support
                    font-size: 0
                    .icon
                        display: inline-block
                        vertical-align: top
                        width: 12px
                        height: 12px
                        margin-right: 4px
                        margin-bottom: 2px
                        margin-top: -1px
                        background-size: 12px 12px
                        background-repeat: no-repeat
                        &.decrease
                            bg-image('decrease_1')
                        &.discount
                            bg-image('discount_1')
                        &.guarantee
                            bg-image('guarantee_1')
                        &.invoice
                            bg-image('invoice_1')
                        &.special
                            bg-image('special_1')
                    .text
                        font-size: 10px
                        line-height: 12px
                        font-weight: 200
            .supports-count
                position: absolute
                right: 12px
                bottom: 18px
                padding: 0 8px
                height: 24px
                line-height: 24px
                border-radius: 14px
                background-color: rgba(0, 0, 0, 0.2)
                text-align: center
                .count
                    vertical-align: top
                    font-size: 10px
                    font-weight: 200
                .icon-keyboard_arrow_right
                    font-size: 10px
                    line-height: 24px
                    margin-left: 2px
        .bulletin-wrapper
            position: relative
            height: 28px
            line-height: 28px
            padding: 0 22px 0 12px
            overflow: hidden
            text-overflow: ellipsis
            white-space: nowrap
            background-color: rgba(7, 17, 27, 0.2)
            .bulletin-icon
                display: inline-block
                vertical-align: top
                margin-top: 7.5px
                width: 22px
                height: 12px
                bg-image('bulletin')
                background-size: 22px 12px
                background-repeat: no-repeat
            .bulletin-text
                vertical-align: top
                font-size: 10px
                font-weight: 200
                margin: 0 4px
            .icon-keyboard_arrow_right
                position: absolute
                top: 9px
                right: 8px
                font-size: 10px
        .background
            position: absolute
            top: 0
            left: 0
            width: 100%
            height: 100%
            z-index: -1
            filter: blur(10px)
            img
                width: 100%
                height: 100%
                object-fit: cover
        .detail
            position: fixed
            top: 0
            left: 0
            width: 100%
            height: 100%
            overflow: auto
            z-index: 100
            opacity: 1
            background-color: rgba(7, 17, 27, 0.8)
            // 动画激活过程
            &.fade-enter-active, &.fade-leave-active
                transition: all 1s
            // 动画启动状态
            &.fade-enter, &.fade-leave-active
                opacity: 0
                background: rbga(7, 17, 27, 0)
            .detail-wrapper
                width: 100%
                min-height: 100%
                .detail-main
                    margin-top: 64px
                    padding-bottom: 64px
                    .name
                        font-size: 16px
                        font-weight: 700
                        text-align: center
                        line-height: 16px
                    .star-wrapper
                        margin-top: 16px
                        padding: 2px 0
                        text-align: center
                    .title
                        display: flex
                        width: 80%
                        margin: 28px auto 24px auto
                        .line
                            flex: 1
                            position: relative
                            top: -6px
                            border-bottom: 1px solid rgba(255, 255, 255, 0.2)
                        .text
                            padding: 0 12px 0 12px
                            font-size: 14px
                            font-weight: 700
                    .supports
                        width: 80%
                        margin: 0 auto
                        .support-item
                            padding: 0 12px
                            margin-bottom: 12px
                            font-size: 0
                            &:last-child
                                margin-bottom: 0
                            .icon
                                display: inline-block
                                width: 16px
                                height: 16px
                                vertical-align: top
                                margin-right: 6px
                                background-size: 16px 16px
                                background-repeat: no-repeat
                                &.decrease
                                    bg-image('decrease_2')
                                &.discount
                                    bg-image('discount_2')
                                &.guarantee
                                    bg-image('guarantee_2')
                                &.invoice
                                    bg-image('invoice_2')
                                &.special
                                    bg-image('special_2')
                            .text
                                font-size: 12px
                                line-height: 16px
                    .bulletin
                        width: 80%
                        margin: 0 auto
                        .text
                            padding: 0 12px
                            font-size: 12px
                            font-weight: 200
                            line-height: 24px
            .detail-close
                position: relative
                width: 32px
                height: 32px
                margin: -64px auto 0 auto
                clear: both
                font-size: 32px

</style>



